import React from 'react';
import * as styles from './Switch.module.css';

type SwitchProps = {
    checked: boolean;
    disabled?: boolean;
    onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
    name?: string;
    id?: string;
    value?: string;
};

const Switch: React.FC<SwitchProps> = ({
                                           checked,
                                           disabled = false,
                                           onChange,
                                           name,
                                           id,
                                           value,
                                       }) => {
    return (
        <label className={styles['switch' as keyof typeof styles]}>
            <input
                type="checkbox"
                role="switch"
                checked={checked}
                onChange={onChange}
                disabled={disabled}
                name={name}
                id={id}
                value={value}
            />
            <span className={styles['slider' as keyof typeof styles]}></span>
        </label>
    );
};

export default Switch;
